import React from 'react';
import { Grid, Container, Typography, CardMedia, List, ListItem,  Link } from '@material-ui/core';
import '../SCSS/About.scss';
import ProfileImg from '../Assets/Images/profile.webp';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCode, faMobileAlt, faImage, faVrCardboard, faTachometerAlt} from '@fortawesome/free-solid-svg-icons'
import Fade from 'react-reveal/Fade';


 function About (){

    return(    
 
        <div>
            <div className="mi-bglines"><span></span><span></span><span></span><span></span><span></span></div>
            <Grid className="about_page">
            <Container maxWidth="lg">
                <Grid className="grid_content">
                  <Typography component="h2" className="title">About Me
                        <Typography component="span"  className="water_mark">About Me</Typography>
                  </Typography>  
                  <Grid container>
                      <Grid item xs={12} sm={6}>
                          <CardMedia image={ProfileImg} className="about-image"></CardMedia>
                      </Grid>
                      <Grid item xs={12} sm={6}>
                          <Grid className="about-content">
                            <Typography component="h3">I am <span className="color-theme">Bharath N</span></Typography>
                            <Typography component="p">A front-end Developer,  and a clean coder, who have excellent expertise in providing pixel-perfect designs along with attractive web animations</Typography>
                            <List>
                                <ListItem>
                                <b>Full Name</b>Bharath N
                                </ListItem>
                                <ListItem>
                                <b>Age</b> 24 Years
                                </ListItem>
                                <ListItem><b>Nationality</b> Indian</ListItem>
                                <ListItem><b>Languages</b>Tamil, English</ListItem>
                                <ListItem><b>Address</b>123/ 1A3 balaji street, Madurai</ListItem>
                            </List>
                                <Link href="http://bharathui.000webhostapp.com/bharath.pdf" className="dnl_cv_btn" download target="_blank"> Download CV</Link>
                          </Grid>
                      </Grid>
                      <Grid item xs={12} className="sceond_section">
                      <Typography component="h2" className="title">SERVICES
                      <Typography component="span"  className="water_mark">SERVICES</Typography>
                      </Typography> 
                            <Grid container
                            >
                                
                                <Grid item xs={12} sm={4} className="pad_enable">
                                    <Fade left>
                                            <Grid className="service">
                                            <FontAwesomeIcon icon={faCode}/>
                                                <h5>UI Developer</h5>
                                                <Typography component="p">
                                                    Two years of experience as a UI Developer
                                                </Typography>
                                            </Grid>
                                    </Fade>
                                </Grid>
                                

                                <Grid item xs={12} sm={4} className="pad_enable">
                                <Grid className="service">
                                <FontAwesomeIcon icon={faMobileAlt}/>
                                  
                                    <h5>Mobile app view</h5>
                                    
                                    <Typography component="p">
                                    Mobile View (Can design sites to adapt for mobiles of all screen sizes)
                                    </Typography>
                                    </Grid>
                                </Grid>

                                <Grid item xs={12} sm={4} className="pad_enable">
                                <Fade right>
                                    <Grid className="service">
                                    <FontAwesomeIcon icon={faImage}/>
                                        <h5>Adobe Photoshop</h5>
                                    
                                    <Typography component="p">
                                         PSD to HTML conversion to slice the images and fonts              
                                    </Typography>
                                    </Grid>
                                </Fade>
                                </Grid>


                                <Grid item xs={12} sm={4} className="pad_enable">
                                    <Fade left>
                                        <Grid className="service">
                                        <FontAwesomeIcon icon={faImage}/>
                                            <h5>Adobe Illustrator</h5>
                                        
                                        <Typography component="p">
                                            Slicing Images, creating custom vector designs, and all basic tool knowledge              
                                        </Typography>
                                        </Grid>
                                    </Fade>
                                </Grid>


                                <Grid item xs={12} sm={4} className="pad_enable">
                                    <Grid className="service">
                                    <FontAwesomeIcon icon={faVrCardboard}/>
                                        <h5>W3 validator</h5>
                                    
                                    <Typography component="p">
                                        W3 Validator (Knowledge in validating all pages using validator.w3.org)           
                                    </Typography>
                                    </Grid>
                                </Grid>

                                <Grid item xs={12} sm={4} className="pad_enable">
                                    <Fade right>
                                        <Grid className="service">
                                        <FontAwesomeIcon icon={faTachometerAlt}/>
                                            <h5>Site optimization</h5>
                                        
                                        <Typography component="p">
                                            To make web page fast on all devices  gtmetrix, google pagespeed
                                        </Typography>
                                        </Grid>
                                    </Fade>
                                </Grid>
                                
                            </Grid>
                      </Grid>
                      </Grid>
                  </Grid>
                 
            </Container>
            </Grid>
        </div>

    )

}
export default About;