import React from 'react';
import '../SCSS/Commen.scss';
import '../SCSS/Contact.scss';
import { Grid, Container, Typography, Button } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
const useStyles = makeStyles((theme) => ({
    root: {
      '& .MuiTextField-root': {
        
        width: '100%',
        borderRadius:'0px',
        marginBottom:"30px"
      },
    },
  }));

 function Contact (){
    const notify = () => toast("Thanks i will reply as soon as possible");
    const classes = useStyles();

    return(

        <div>
            <div className="mi-bglines"><span></span><span></span><span></span><span></span><span></span></div>
             <Grid className="contact_page page_top">
                <Container maxWidth="lg">
                    <Grid className="grid_content"> 
                        <Typography component="h2" className="title">CONTACT ME
                            <Typography component="span"  className="water_mark">CONTACT ME</Typography>
                        </Typography>

                        <Grid container>
                            <Grid item xs={12} sm={6} className="input_field">

                                <Typography component="h3">Get In Touch</Typography>

                           
                         <form className={classes.root} noValidate autoComplete="off" >
                            <TextField
                                required
                                id="outlined-required"
                                label="Enter your Name"
                                defaultValue=""
                                variant="outlined"
                            />
                             <TextField
                                required
                                id="outlined-required"
                                label="Enter your Email"
                                defaultValue=""
                                variant="outlined"
                            />
                             <TextField
                                required
                                id="outlined-required"
                                label="Enter your Message"
                                defaultValue=""
                                variant="outlined"
                                multiline
                                rows={9}
                            />
                            <Button onClick={notify} variant="contained" className="submit_btn">
                                Send Mail
                            </Button>
                            <ToastContainer />
                            </form>
                            </Grid>
                            <Grid item xs={12} sm={6} className="info">
                                <Grid className="infoblock">
                                    <Typography component="span"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M22 16.92v3a2 2 0 0 1-2.18 2 19.79 19.79 0 0 1-8.63-3.07 19.5 19.5 0 0 1-6-6 19.79 19.79 0 0 1-3.07-8.67A2 2 0 0 1 4.11 2h3a2 2 0 0 1 2 1.72 12.84 12.84 0 0 0 .7 2.81 2 2 0 0 1-.45 2.11L8.09 9.91a16 16 0 0 0 6 6l1.27-1.27a2 2 0 0 1 2.11-.45 12.84 12.84 0 0 0 2.81.7A2 2 0 0 1 22 16.92z"></path></svg></Typography>
                                    <Grid className="content">
                                        <Typography component="h6">Phone</Typography>
                                        <Typography component="p">+0123-3456-7891</Typography>
                                    </Grid>
                                </Grid>

                                <Grid className="infoblock">
                                    <Typography component="span"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M4 4h16c1.1 0 2 .9 2 2v12c0 1.1-.9 2-2 2H4c-1.1 0-2-.9-2-2V6c0-1.1.9-2 2-2z"></path><polyline points="22,6 12,13 2,6"></polyline></svg></Typography>
                                    <Grid className="content">
                                        <Typography component="h6">Email</Typography>
                                        <Typography component="p">bharath0610@gmail.com</Typography>
                                    </Grid>
                                </Grid>


                                <Grid className="infoblock">
                                    <Typography component="span"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M21 10c0 7-9 13-9 13s-9-6-9-13a9 9 0 0 1 18 0z"></path><circle cx="12" cy="10" r="3"></circle></svg></Typography>
                                    <Grid className="content">
                                        <Typography component="h6">Address</Typography>
                                        <Typography component="p">121 balaji Street, alagappan nagar, Madurai 625003.</Typography>
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </Container>
            </Grid>
        </div>

    )

}
export default Contact;