import React  from "react";
// import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import CssBaseline from '@material-ui/core/CssBaseline';
// import Divider from '@material-ui/core/Divider';
import Drawer from '@material-ui/core/Drawer';
import Hidden from '@material-ui/core/Hidden';
import IconButton from '@material-ui/core/IconButton';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import MenuIcon from '@material-ui/icons/Menu';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import {  Grid, Card, CardMedia } from '@material-ui/core';
import {  NavLink } from "react-router-dom";
import '../SCSS/Sidebar.scss';
import ProfileImg from '../Assets/Images/profile.webp';
import classNames from "classnames";




const drawerWidth = 300 ;

const useStyles = makeStyles(theme => ({
    root: {
      display: 'flex',
    },
    drawer: {
      [theme.breakpoints.up('sm')]: {
        width: drawerWidth,
        flexShrink: 0,
      },
    },
    appBar: {
      [theme.breakpoints.up('sm')]: {
        width: `calc(100% - ${drawerWidth}px)`,
        marginLeft: drawerWidth,
      },
    },
    menuButton: {
      marginRight: theme.spacing(2),
      [theme.breakpoints.up('sm')]: {
        display: 'none',
      },
    },
    toolbar: theme.mixins.toolbar,
    drawerPaper: {
      width: drawerWidth,
    },
    content: {
      flexGrow: 1,
      padding: theme.spacing(3),
    },
  }));
  

function SideBarComponent (){

  // const handleMenuActiveChange = menu => {
  //   setMenuActive(menu);
  //   console.log(menu);
  // };

  // const [menuActive, setMenuActive] = useState("");
    const { container } = "props";
    
    const classes = useStyles();
    const theme = useTheme();
    const [mobileOpen, setMobileOpen] = React.useState(false);

    
    const handleDrawerToggle = () => {
      setMobileOpen(!mobileOpen);
    };


    const [width, setWidth] = React.useState(window.innerWidth);
    // const [height, setHeight] = React.useState(window.innerHeight);

    const scrollTop = () => {
      window.scrollTo(0, 0);
      setWidth(window.innerWidth);
      console.log('dasknd',mobileOpen);
      console.log(width,'123214')
      if (  width <= 767 ) {
      console.log(width,'sdsds')
      setMobileOpen(!mobileOpen);
      }else if(width > 768){
        // mobileOpen = true;
        console.log(width,'else')
        setMobileOpen(false);
      }
    }

  //   React.useEffect(() => {
  //     window.addEventListener("resize", scrollTop);
  //     return () => window.removeEventListener("resize", scrollTop);
  // });

    // const Page = ({ title }) => (
    //   <div>
    //     <h1>{ title }</h1>
       
    //   </div>
    // );
// const Home = () => <Page title="Home" />;
// const About = () => <Page title="About" />;
// const Resume = () => <Page title="Resume" />;
// const Projects   = () => <Page title="Projects" />;
// const Contact = () => <Page title="Contact" />;



    const drawer =  (

        <div className={classNames(classes.nameadd, "displ_new")}>
         
          <div  className={classNames(classes.toolbar, "img_sec")}>
            <Card className="card_prf">
              <CardMedia
              image={ProfileImg}
              className="profile_img"
              ></CardMedia>
            </Card>
          </div>
          
          <List className="sideMenu"> 
              <ListItem  onClick={()=> scrollTop()}>
                  <NavLink to='/' className="normal" activeClassName="active" exact> 
                    Home
                    </NavLink>
                </ListItem>
              <ListItem onClick={()=> scrollTop()}>
                  <NavLink to='/about' className="normal" activeClassName="active" exact> 
                      About 
                  </NavLink>
                </ListItem>
              <ListItem onClick={()=> scrollTop()}>
                <NavLink  to='/resume'  className="normal" activeClassName="active" exact>
                      Resume
                </NavLink>
              </ListItem>
              <ListItem onClick={()=> scrollTop()}>
                 <NavLink  to='/projects' className="normal" activeClassName="active" exact>
                      Projects
                  </NavLink> 
              </ListItem>
              <ListItem onClick={()=> scrollTop()}> 
                <NavLink  to='contact' className="normal" activeClassName="active" exact>
                      Contact
                </NavLink>  
              </ListItem>
          </List>
          {/* <Divider /> */}
          <Grid className="copyright">© 2020 BharathUI</Grid> 
        </div>
        
      );


    return(

       <div className={classes.root}>
      <CssBaseline />
      <Grid className="mobile-resposive">
      
      <AppBar position="fixed" className={classes.appBar}>
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            className={classes.menuButton}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" noWrap>
            Welcome
          </Typography>
        </Toolbar>
      </AppBar>
      
</Grid>      

      <nav className={classes.drawer} aria-label="mailbox folders">
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Hidden smUp implementation="css">
          <Drawer
          
            container={container}
            variant="temporary"
            anchor={theme.direction === 'rtl' ? 'right' : 'left'}
            open={mobileOpen}
            onClose={handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
            
          >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden xsDown implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
            {drawer}
          </Drawer>
        </Hidden>
      </nav>
          
    </div>

    )

}

export default SideBarComponent;